import authorize from '@/components/authorize'
import api from '@/utils/api.js'
import dateFilter from '@/filter/dateFilter.js'
import utils from '@/utils/utils.js'
var app = getApp();

export default {
	components: {
		authorize
	},
	data() {
		return {
			code:'',
			isAuthorizeShow: false,
			autoplay: true,
			interval: 3000,
			duration: 1000,
			imgMode: 'aspectFit',
			keyWordDisabled: true,
			priceStarDisabled: true,
			today: '',
			searchCondition: {
				address: '贵阳',
				checkIn: '',
				checkOut: '',
			},
			hotelList: []
		}
	},
	onLoad() {
		
	},
	/**
	 * 页面显示
	 */
	onShow() {
		let _now_day = new Date();
		_now_day.setHours(0);
		_now_day.setMinutes(0);
		_now_day.setSeconds(0);
		_now_day.setMilliseconds(0);
		// this.globalData.today = Date.parse(_now_day);
		// 入住及离店
		let _checkOut = new Date();
		_checkOut.setHours(0);
		_checkOut.setMinutes(0);
		_checkOut.setSeconds(0);
		_checkOut.setMilliseconds(0);
		_checkOut.setDate(_checkOut.getDate() + 1)
		this.searchCondition.checkIn = this.formatDate(Date.parse(_now_day))
		this.searchCondition.checkOut = this.formatDate(Date.parse(_checkOut))
		let that = this
		utils.checkSession().then(res=>{
			// that.goLogin();
		}).catch(res=>{
			that.isAuthorizeShow = true
		});
	},
	onReachBottom() {
		console.log("滑动到页面底部")
		this.getData();
	},
	onPullDownRefresh() {
		console.log("下拉刷新");
		this.refreshing = true;
		this.getData();
	},
	methods: {
		getData() {
			
		},
		goDetail(e) {
			uni.navigateTo({
				url: "../detail/detail?data=" + encodeURIComponent(JSON.stringify(e))
			})
		},
		// 将时间戳转化为年月日星期
		formatDate(date) {
			return dateFilter.formatDate(date)
		},
		pickDate() {
			let checkIn = JSON.stringify(this.searchCondition.checkIn)
			let checkOut = JSON.stringify(this.searchCondition.checkOut)
			uni.navigateTo({
				url: "../selectDate/selectDate?checkIn=" + checkIn + "&checkOut=" + checkOut
			})
		},
		// 获取个人信息
		getUserInfo(res) {
			console.log(res)
			let globalData = app.$vm.globalData
			globalData.userInfo = res.userInfo;
			api.authorize({
				appid:globalData.appid,
				code:globalData.code,
				encryptedData:res.encryptedData,
				iv:res.iv
			}).then((res)=>{
				console.log(res)
			});
			
		},
		share(e) {
			if (this.providerList.length === 0) {
				uni.showModal({
					title: "当前环境无分享渠道!",
					showCancel: false
				})
				return;
			}
			let itemList = this.providerList.map(function(value) {
				return value.name
			})
			uni.showActionSheet({
				itemList: itemList,
				success: (res) => {
					uni.share({
						provider: this.providerList[res.tapIndex].id,
						scene: this.providerList[res.tapIndex].type && this.providerList[res.tapIndex].type === 'WXSenceTimeline' ?
							'WXSenceTimeline' : "WXSceneSession",
						type: 0,
						title: "uni-app模版",
						summary: e.title,
						imageUrl: e.img_src,
						href: "https://uniapp.dcloud.io",
						success: (res) => {
							console.log("success:" + JSON.stringify(res));
						},
						fail: (e) => {
							uni.showModal({
								content: e.errMsg,
								showCancel: false
							})
						}
					});
				}
			})
		}
	}
}
